import React, { useState, useEffect } from "react";
import { Meme } from "../Meme/Meme";
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';

const objectToQueryParam = obj => {
  const params = Object.entries(obj).map(([key, value]) => `${key}=${value}`);
  return "?" + params.join("&");
};

function App() {
  const [templates, setTemplates] = useState([]);
  const [template, setTemplate] = useState(null);
  const [topText, setTopText] = useState("");
  const [bottomText, setBottomText] = useState("");
  const [meme, setMeme] = useState(null);

  useEffect(() => {
    fetch("https://api.imgflip.com/get_memes").then(x =>
      x.json().then(response => setTemplates(response.data.memes))
    );
  }, []);

  if (meme) {
    return (
      <Container style={{ textAlign: "center" }}>
        <img style={{ width: 300 }} src={meme} alt="custom meme" />
      </Container>
    );
  }

  return (
    <div style={{ textAlign: "center" }}>
      {template && (
        <Container maxWidth='xs'>
          <Stack spacing={2}>
            <Meme template={template} />
            <TextField id="outlined-basic" label='Caption 1' variant="outlined" value={topText} onChange={e => setTopText(e.target.value)}/>
            <TextField id="outlined-basic" label='Caption 2' variant="outlined" value={bottomText} onChange={e => setBottomText(e.target.value)}/>
            <Button 
              variant="contained"
              onClick={ async e => {
                e.preventDefault();
                const params = {
                  template_id: template.id,
                  text0: topText,
                  text1: bottomText,
                  username: "Colby7432",
                  password: "abcdefg321"
                };
                const response = await fetch(
                  `https://api.imgflip.com/caption_image${objectToQueryParam(
                    params
                  )}`
                );
                const json = await response.json();
                setMeme(json.data.url);
              }}
            >
              Create Meme
            </Button>
          </Stack>
        </Container>
      )}
      {!template && (
        <>
          <Typography variant='h3' gutterBottom>
            Pick A Template
          </Typography>
          {templates.map(template => {
            return (
              <>
              { template.box_count < 4 && (
                <Meme
                  template={template}
                  onClick={() => {
                    setTemplate(template);
                  }}
                />
              )}
              </>
            );
          })}
        </>
      )}
    </div>
  );
}

export default App;